* {
  margin: 0;
}
.app__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
.app__header > h1 {
  color: rgb(206, 10, 10);
  font-size: 2rem;
}
.app__dropdown {
  background-color: white;
}
.app__stats {
  display: flex;
  justify-content: space-between;
}
.app {
  display: flex;
  justify-content: space-evenly;
}
.app__left {
  flex: 0.9;
}
.app__right {
  display: flex;
  flex-direction: column;
}
.app__right .MultiCardContent-root {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.app__graph {
  flex-grow: 1;
}
.app__graphTitle {
  margin-top: 20px;
  margin-bottom: 20px;
}
@media (max-width: 990px) {
  .app {
    flex-direction: column;
  }
}
